import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import html2canvas from "html2canvas";


import $ from "jquery";
import {Utils} from '../../helper'

import './style.scss';


const tree = {
  "" : {
    "":{
      "":{
        "":{
          "":{
            "":["", "", "", "", "", "", ""]
          },
          "":{
            "":[""],
            "":[""],
            "":[""]
          },
          "":["",""],
          "":{
            "":{
              "":["", "", " "]
            },
            "":["","",""],
            "":[" "],
            "":["", ""]
          }
        },
        "":{
          "":["",""],
          "":["", "", "", ""]
        }
      }
    }
  }
}


function getValue(input,level){
  if(!Array.isArray(input)){
    let _input = Object.entries(input);
    return _input.map((item, i) => {
      let _output =  getValue(item[1],level+1);
      _output.push({name:item[0],level:level});
      return _output;
    });
  }else {
    return input.map(i=>{
      return {name:i,level:level+1}
    })
  }
}
const allNames =  getValue(tree,0).flat(Infinity).sort((a, b) => a.level - b.level);


class FamiltyTree extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.state = {

    }
  }

  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentDidMount(){
    document.title = "Editor | Family tree";
  }
  componentWillUnmount(){
    this.unmounted = true;
    document.title = "Editor | Home";
  }

  renderName(){
    return allNames.map((item,i)=>{
      return <p className={`level-${item.level}`} key={i}>{item.name}</p>
    })
  }

  async downloadDiv(){
    try {
      let canvas = await html2canvas(document.getElementById("html-content-holder"),
        {
          backgroundColor: null,
          scale:3
        }
      );
      let anchorTag = document.createElement("a");
      document.body.appendChild(anchorTag);
      anchorTag.download = "filename.png";
      anchorTag.href = canvas.toDataURL();
      anchorTag.target = '_blank';
      anchorTag.click();
      document.body.removeChild(anchorTag);
    } catch (e) {
      console.log(e);
    }
  }

  render(){

    return(
      <div className="section__model ">
        <div className="family__container" id="html-content-holder">
        {this.renderName()}
        </div>
        <button onClick={this.downloadDiv}>Download</button>

      </div>

    )
  }
}

export default FamiltyTree;
