import React, {useState } from 'react'

import './styles.scss';
import {Utils,Filter,Transform} from '../../helper'
import FilterImage from '../../images/filter2.jpg';
import {ReactComponent as SettingIcon} from '../../images/setting.svg';

function FilterBody(props){
  return <div className="image__filter__options">{props.children}</div>
}

function Brightness(props){
  const handler = event => {
    let value = event.target.value;
    props.handler({brightness:Utils.inputNumber(value,0)});
  }
  let _w = Math.floor(props.brightness*100/200)+"%";
  return(
    <div className="filter__range">
      <p>Brightness</p>
      <input type="range" min="0" max="200" value={props.brightness} onChange={handler}  style={{"--w":_w}}/>
      <span>{props.brightness}%</span>
    </div>
  )
}

