function CssFilters(props){

  let {adjust,isProcess} = props?.data??{};
  let filterName = adjust?.name??"";

  const [isChild,setChild] = useState(false);

  const handleSelect = (name,value) => {
    if(!!isProcess || !props.handler) return;
    if(filterName === name && !!name){
      setChild(!isChild);
    }else {
      setChild(!name?false:isChild);
      props.handler({adjust:value});
    }
  }

  const renderFilters = () =>{
    return Filter.cssFilters.map((item,i)=>{
      let{name,style,value} = item;
      value.name = name;

      return <div className={`css__filter${filterName === name?" active":""}`} onClick={()=>handleSelect(name,value)} key={i}>
        {filterName === name && <div className="css__filter__setting"><SettingIcon/></div>}
        <img src="../editor/img/filter2.jpg" alt="img" style={{filter:style}} />
        <p>{name}</p>
      </div>
    })
  }

  const handleAdjust = (value) =>{
    if(!!isProcess || !props.handler) return;
    props.handler({adjust:{...adjust,...value}});
  }

  let hasBrightness = adjust.brightness  !== undefined;
  let hasSaturate = adjust.saturate  !== undefined;
  let hasInvert = adjust.invert  !== undefined;
  let hasContrast = adjust.contrast  !== undefined;
  let hasSepia = adjust.sepia  !== undefined;
  let hasBlur = adjust.blur  !== undefined;
  let hasHue = adjust.hue  !== undefined;
  let hasGrayscale = adjust.grayscale  !== undefined;


  return(
    <div className="css__filters">

      {!!isChild && <div className="css__filters__ranges">
        {!!hasBrightness && <Brightness handler={handleAdjust} brightness={adjust.brightness}/>}
        {!!hasSaturate && <Saturation handler={handleAdjust} saturate={adjust.saturate}/>}
        {!!hasInvert && <Inversion handler={handleAdjust} invert={adjust.invert}/>}
        {!!hasContrast && <Contrast handler={handleAdjust} contrast={adjust.contrast}/>}
        {!!hasSepia && <Sepia handler={handleAdjust} sepia={adjust.sepia}/>}
        {!!hasBlur && <Blur handler={handleAdjust} blur={adjust.blur}/>}
        {!!hasHue && <HueRotate handler={handleAdjust} hue={adjust.hue}/>}
        {!!hasGrayscale && <Grayscale handler={handleAdjust} grayscale={adjust.grayscale}/>}
      </div>}

      <div className={`css__filters__body${!!isChild?" active":""}`}>
      <div className={`css__filter${!filterName?" active":""}`} onClick={()=>handleSelect("",{})}>
        <img src="../editor/img/filter2.jpg" alt="img" />
        <p>None</p>
      </div>
        {renderFilters()}
      </div>
    </div>
  )
}


function CanvasFilters(props){

  let {filter,isProcess} = props?.data??{};

  const [isChild,setChild] = useState(false);

  const handleSelect = (value) => {
    if(!!isProcess || !props.handler) return;

    let _cName = filter?.name??"";
    let _nName = value?.name??"";

    if(!_cName && !_nName) return;

    if(_cName === _nName){
      if(!_cName) return;

      let {child} = Filter.canvasFilters.find(i=>i.name === _nName);
      if(!!child && child > 1){
        setChild(!isChild);
      }
    }else {
      setChild(!_nName?false:isChild);
      props.handler(value);
    }
  }


  const handleChild = (index) => {
    if(!!isProcess || !props.handler) return;
    let _filter = filter;
    _filter.index = index;
    props.handler(_filter);
  }

  const renderOptions = () =>{
    return Filter.canvasFilters.map((item,i)=>{
      let{name,child} = item;
      let _filter = {name};
      if(!!child){_filter.index=0;}
      let _style = {
        "backgroundImage": "url(../editor/img/canvas-filter.jpg)",
        "backgroundPosition" : `-${i*60}px 0`
      }
      return <div className={`css__filter${filter.name === name?" active":""}`} onClick={()=>handleSelect(_filter)} key={name}>
        {!!child && filter.name === name && <div className="css__filter__setting"><SettingIcon/></div>}
        <div className="img" style={_style}></div>
        <p>{name}</p>
      </div>
    })
  }

  const renderChilds = () =>{
    let _index = Filter.canvasFilters.findIndex(i=>i.name === filter.name);
    if(_index === -1) return;
    let {name,child} = Filter.canvasFilters[_index];

    return new Array(child).fill(undefined).map((_i,i)=>{
      let _style = {
        "backgroundImage": "url(../editor/img/canvas-filter.jpg)",
        "backgroundPosition" : `-${i*40}px -${60+_index*40}px`
      }
      return <div className="css__filters__option" onClick={()=>handleChild(i)} key={name+i} style={_style}></div>
    })
  }

  return(
    <div className="css__filters">

      {!!isChild && <div className="css__filters__options">
        {renderChilds()}
      </div>}

      <div className="css__filters__body">
        <div className={`css__filter${!filter.name?" active":""}`} onClick={()=>handleSelect({})}>
          <img src="../editor/img/filter2.jpg" alt="img" />
          <p>None</p>
        </div>
        {renderOptions()}
      </div>
    </div>
  )
}


function AdvFilters(props){

  let {transform,isProcess} = props?.data??{};
  const [isChild,setChild] = useState(false);


  const handleSelect = (value) => {
    if(!!isProcess || !props.handler) return;

    let _cName = transform?.name??"";
    let _nName = value?.name??"";

    if(!_cName && !_nName) return;

    if(_cName === _nName){
      if(!_cName) return;

      let {child} = Transform.methods.find(i=>i.name === _nName);
      if(!!child && child > 1){
        setChild(!isChild);
      }
    }else {
      // console.log(value);
      setChild(!_nName?false:isChild);
      props.handler(value);
    }
  }

  const handleChild = (index) => {
    if(!!isProcess || !props.handler) return;
    let _transform = transform;
    _transform.index = index;
    props.handler(_transform);
  }

  const renderOptions = () =>{
    return Transform.methods.map((item,i)=>{
      let{name,child} = item;
      let _transform = {name};
      if(!!child){_transform.index=0;}
      let _style = {
        "background": `url(../editor/img/transform.jpg)`,
        "backgroundPosition" : `-${i*60}px 0`,
      }
      return <div className={`css__filter${transform.name === name?" active":""}`} onClick={()=>handleSelect(_transform)} key={name}>
        {!!child && transform.name === name && <div className="css__filter__setting"><SettingIcon/></div>}
        <div className="img" style={_style}></div>
        <p>{name}</p>
      </div>
    })
  }

  const renderChilds = () =>{
    let _index = Transform.methods.findIndex(i=>i.name === transform.name);
    if(_index === -1) return;
    let {name,child} = Transform.methods[_index];

    return new Array(child).fill(undefined).map((_i,i)=>{
      let _style = {
        "backgroundImage": `url(../editor/img/transform.jpg)`,
        "backgroundPosition" : `-${i*40}px -${60+_index*40}px`
      }
      return <div className="css__filters__option" onClick={()=>handleChild(i)} key={name+i} style={_style}></div>
    })
  }

  return(
    <div className="css__filters">

      {!!isChild && <div className="css__filters__options">
        {renderChilds()}
      </div>}

      <div className="css__filters__body">
        <div className={`css__filter${!transform.name?" active":""}`} onClick={()=>handleSelect({})}>
          <img src="../editor/img/filter2.jpg" alt="img" />
          <p>None</p>
        </div>
        {renderOptions()}
      </div>
    </div>
  )
}


export {
  FilterBody,
  CssFilters,
  CanvasFilters,
  AdvFilters,
}
