import React, {useState } from 'react'

import './styles.scss';
import {Utils} from '../../helper'

import {ReactComponent as LockIcon} from '../../images/lock.svg';
import {ReactComponent as CropIcon} from '../../images/crop.svg';
import {ReactComponent as AdjustIcon} from '../../images/adjust.svg';
import {ReactComponent as FilterIcon} from '../../images/filter.svg';
import {ReactComponent as TransformIcon} from '../../images/transform.svg';
import {ReactComponent as SettingIcon} from '../../images/setting2.svg';


function RenderMenu(props){

  let {menu,isProcess} = props?.data??{};
  const handler = (name) =>{
    if(!!isProcess) return;
    props.handler({menu:name})
  }
  return (
    <div className="edit__type__menu">
      <div className={`edit__type__menu__item${menu === "crop"?" active":""}`} onClick={()=>handler("crop")}>
        <span><CropIcon/></span>
        <p>Crop</p>
      </div>
      <div className={`edit__type__menu__item${menu === "adjust"?" active":""}`} onClick={()=>handler("adjust")}>
        <span><AdjustIcon/></span>
        <p>Adjust</p>
      </div>
      <div className={`edit__type__menu__item${menu === "filter"?" active":""}`} onClick={()=>handler("filter")}>
        <span><FilterIcon/></span>
        <p>Filter</p>
      </div>
      <div className={`edit__type__menu__item${menu === "transform"?" active":""}`} onClick={()=>handler("transform")}>
        <span><TransformIcon/></span>
        <p>Transform</p>
      </div>
      <div className={`edit__type__menu__item${menu === "setting"?" active":""}`} onClick={()=>handler("setting")}>
        <span><SettingIcon/></span>
        <p>Setting</p>
      </div>
    </div>
  )
}

function EditBody(props){
  return <div className="image__edit__options">{props.children}</div>
}

function QualityFactor(props){

  const handleQuality = event => {
    let isProcess = props?.data?.isProcess??false;
    if(!!isProcess) return;

    let value = event.target.value;
    value = Utils.inputNumber(value,1);
    value = value >= 100?100:value;
    if(props.handler){
      props.handler({quality:value});
    }
  }

  let quality = props?.data?.quality??90;

  return(
    <div className="image__edit__option">
      <p className="image__edit__option__title"><span>Quality</span>:</p>
      <div className="image__edit__option__input">
        <input className="input-sm" type="number" value={quality} onChange={handleQuality}/>
        <span>%</span>
      </div>
    </div>
  )
}

function CropOptions(props){

  const handleCrop = name => {
    let {isProcess} = props?.data??{};
    if(!!isProcess) return;
    if(props.handler){
      props.handler({crop:name});
    }
  }

  let crop = props?.data?.crop??"";

  return(
    <div className="image__edit__option">
      <div className="image__edit__option__crops">
        <div className={`crops__each${crop === "cc"?" active":""}`} onClick={()=>handleCrop("cc")}> <span className="c-custom"></span><p>Custom</p> </div>
        <div className={`crops__each${crop === "c11"?" active":""}`} onClick={()=>handleCrop("c11")}> <span className="c-11"></span><p>1:1</p> </div>
        <div className={`crops__each${crop === "c34"?" active":""}`} onClick={()=>handleCrop("c34")}> <span className="c-34"></span><p>3:4</p> </div>
        <div className={`crops__each${crop === "c43"?" active":""}`} onClick={()=>handleCrop("c43")}> <span className="c-43"></span><p>4:3</p> </div>
        <div className={`crops__each${crop === "c23"?" active":""}`} onClick={()=>handleCrop("c23")}> <span className="c-23"></span><p>2:3</p> </div>
        <div className={`crops__each${crop === "c32"?" active":""}`} onClick={()=>handleCrop("c32")}> <span className="c-32"></span><p>3:2</p> </div>
      </div>
    </div>
  )
}


function FixedSizeInput(props){

  const handleSize = event => {
    let{canvas,isProcess} = props?.data??{};
    if(!!isProcess || !canvas) return;

    let _rWidth = canvas.width;
    let _rHeight = canvas.height;

    const name = event.target.name;
    let value = event.target.value;
    value = Utils.inputNumber(value,1);
    value = value >= 9999?9999:value;

    let width = 0;
    let height = 0;

    if(name === "width"){
      width = value;
      height = !value?"":Math.ceil(_rHeight*value/_rWidth);
      height = height >= 9999?9999:height;
      if( height === 9999){
        width = Math.ceil(_rWidth*9999/_rHeight);
      }
    }else {
      height = value;
      width = !value?"":Math.ceil(_rWidth*value/_rHeight);
      width = width >= 9999?9999:width;
      if( width === 9999){
        height = Math.ceil(_rHeight*9999/_rWidth);
      }
    }

    if(props.handler){
      props.handler({width,height});
    }
  }

  let {width,height} = props?.data??{};

  return(
    <div className="image__edit__option">
      <p className="image__edit__option__title"><span>Size</span>:</p>
      <div className="image__edit__option__input">
        <input className="input-md" type="number" value={width} name="width" onChange={handleSize}/>
        <span>px</span>
      </div>
      <span className="image__edit__option__input-divider"><LockIcon/></span>
      <div className="image__edit__option__input">
        <input className="input-md" type="number" value={height} name="height" onChange={handleSize}/>
        <span>px</span>
      </div>
    </div>
  )
}


function FormateSelector(props){
  const [isActive, setActive] = useState(false);

  const handleActive = () =>{
    let isProcess = props?.data?.isProcess??false;
    if(!!isProcess) return;
    setActive(!isActive);
  }
  const handleFormate = option => {
    let isProcess = props?.data?.isProcess??false;
    if(!!isProcess) return;

    if(props.handler){
      props.handler({formate:option});
    }
  }
  let formates = {"jpg":"JPG","png":"PNG"}
  let formate = props?.data?.formate??"jpg";

  return(
    <div className="image__edit__option">
      <p className="image__edit__option__title"><span>Formate</span>:</p>
      <div className="image__edit__option__select">
      <div className={`select${isActive?" active":""}`} onClick={handleActive}>
        <div className="select-head">
          <div className="select-selected">{formates[formate]}</div>
          <div className="select-toggle"><svg width="512" height="298" viewBox="0 0 512 298.04"><path d="M12.08 70.78c-16.17-16.24-16.09-42.54.15-58.7 16.25-16.17 42.54-16.09 58.71.15L256 197.76 441.06 12.23c16.17-16.24 42.46-16.32 58.71-.15 16.24 16.16 16.32 42.46.15 58.7L285.27 285.96c-16.24 16.17-42.54 16.09-58.7-.15L12.08 70.78z"/></svg></div>
        </div>
        {isActive && <div className="select-options">
          <div className="select-option" onClick={()=>handleFormate("jpg")}>JPG</div>
          <div className="select-option" onClick={()=>handleFormate("png")}>PNG</div>
        </div>}
      </div>
      </div>
    </div>
  )
}


export {
  RenderMenu,
  EditBody,
  QualityFactor,
  CropOptions,
  FixedSizeInput,
  FormateSelector,
}
