import React, { Component,Fragment,useState } from 'react'
import { Link } from 'react-router-dom'
import { createCanvas, loadImage } from 'canvas';

import $ from "jquery";
import {Utils} from '../../helper'

import './style.scss';

import {FilePicker,FilePreview,EditOptions} from '../../components'

import DummyImage from '../../images/adiyogi.webp';


class PhotoCard extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.blank = {
      file:"",
      canvas:"",
      quality:90,
      width:0,
      height:0,
      formate:"jpg",
      hFlip:false,
      vFlip:false,
      turn:0,
      rotate:0,
      crop:"",
      isProcess:false,
    }

    this.state = this.blank
  }

  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentDidMount(){
    document.title = "Editor | Resize Image";

    let _self = this;
    fetch(DummyImage).then(res=>{
      res.blob().then((blob) => {
        const file = new File([blob], 'adiyogi.webp', {type: blob.type});
        _self.handleFile(file)
      })
    })

  }

  componentWillUnmount(){
    this.unmounted = true;
    document.title = "Editor | Home";
  }

  handleFile = async(file) =>{
    try {
      // console.log(file);
      let image = await loadImage(URL.createObjectURL(file))

      let width = image.width;
      let height = image.height;

      let canvas = createCanvas(width, height);
      let ctx = canvas.getContext('2d');

      ctx.drawImage(image, 0, 0);

      this.setState({file,canvas,width,height});
    } catch (e) {
      this.setState(this.blank);
    }
  }

  handleOptions = (option) =>{
    let{file,isProcess} = this.state;
    if(!!isProcess) return;

    if(option === "new"){
      this.setState(this.blank);
    }else if (option === "reset") {
      this.setState(this.blank,()=>{
        this.handleFile(file);
      });
    }else if (option === "download") {
      this.handleDownload();
    }

  }

   handleDownload = async() =>{

     try {
      let{file,canvas,width, height,quality,formate,isProcess} = this.state;
      if(!!isProcess) return;
      this.setState({isProcess:true});

      quality = quality || 90;
      quality = quality/100;

      let _newCanvas = createCanvas(width, height);
      let ctx = _newCanvas.getContext('2d');
      ctx.drawImage(canvas, 0, 0,width, height);

      this.setState({isProcess:false});

      // console.log(canvas);
      if(!canvas) return;

      let name = `${file.name.split(".")[0]}.${formate}`;
      let type = formate === "jpg"?"image/jpeg":"image/png";

      let _url = await new Promise(resolve => {
        _newCanvas.toBlob(resolve,type,quality);
      });

      let _a = document.createElement("a");
      _a.download = name;
      _a.href = URL.createObjectURL(_url);
      _a.target = '_blank';
      _a.style.display = 'none';
      document.body.appendChild(_a);
      _a.click();
      document.body.removeChild(_a);

     } catch (e) {
       console.log(e);
       this.setState({isProcess:false});
     }
  }

  render(){
    let{file,canvas,quality,width,height,turn,rotate,formate,hFlip,vFlip,crop,isProcess} = this.state;
    return(
      <div className="section__model section__model_row" style={{"marginTop":"80px"}}>
        {!file && <FilePicker.SelectImage handler={this.handleFile}/>}
        {!!file &&
          <Fragment>
            <div className="section__model_half">
              <FilePreview.ImagePreview
                handler={this.handleOptions}
                close={val=>this.setState(val)}
                data={{file,canvas,width,height,crop,rotate,isProcess}}
                key={crop+turn}
              />
            </div>
            <div className="section__model_half">

              <EditOptions.EditBody>
                
              </EditOptions.EditBody>


            </div>
          </Fragment>
        }
      </div>
    )
  }
}

export default PhotoCard;
