import { createCanvas,loadImage} from 'canvas';


  const getViewSize = (w,h) => {
    let max = 1000;
    function factor(_w, _h) {
      if(_w%2 === 0 && _h%2 === 0) return 2;
      if(_w%3 === 0 && _h%3 === 0) return 3;
      if(_w%5 === 0 && _h%5 === 0) return 5;
      if(_w%7 === 0 && _h%7 === 0) return 7;
      return 1;
    }
    while (w > max || h > max) {
      let _gcd = factor(w,h);
      if(_gcd !== 1){
        w = Math.floor(w/_gcd);
        h = Math.floor(h/_gcd);
      }else {
        if(w>=h){
          h = Math.floor(max*h/w);
          w = max;
        }else {
          h = max;
          w = Math.floor(max*w/h);
        }
      }
    }
    return [w,h];
  }

  const rotatedWidth = (w,h,r) =>{
    if(!r) return w;

    let _l = Math.sqrt((h**2)+(w**2));
    let _r =  Math.asin(h/_l);
    let _w = w;

    if(w>h){
      let _radian = _r + Math.abs(r) * Math.PI/180;
      _w = Math.ceil(Math.ceil(Math.sin(_radian) * _l)*w/h);
    }else {
      let _radian = _r - Math.abs(r) * Math.PI/180;
      _w = Math.ceil(Math.cos(_radian) * _l);
    }
    return _w;
  }

  const getCss = (width,height,rotate,hF,vF,turn) =>{

    let _w = rotatedWidth(width,height,rotate);

    let _css = {width:_w};
    let _transform = "translate(-50%,-50%)";

    if(!!rotate || !!turn){
      _transform += ` rotate(${rotate+turn}deg)`
    }

    if(turn === 90 || turn === 270){
      _css.width = Math.floor(height*_w/width);
    }

    if(!!hF){
      _transform +=  " scaleX(-1)";
    }
    if(!!vF){
      _transform +=  " scaleY(-1)";
    }

    _css.transform = _transform;

    return _css;
  }

  const downloadFile = async(canvas,formate,quality)=>{
    try {
      let type = formate === "jpg"?"image/jpeg":"image/png";

      quality = quality || 90;
      quality = quality/100;

      let url = await new Promise(resolve => {canvas.toBlob(resolve,type,quality)});

      let anchor = document.createElement("a");
      anchor.download = `${Date.now()}.${formate}`;
      anchor.href = URL.createObjectURL(url);
      anchor.target = '_blank';
      anchor.style.display = 'none';
      document.body.appendChild(anchor);
      anchor.click();
      document.body.removeChild(anchor);
    } catch (e) {
       console.log(e);
    }
  }

  const flipCanvas = (canvas,hFlip,vFlip) =>{
    if(!hFlip && !vFlip) return canvas
    let _tx = !!hFlip?canvas.width:0;
    let _ty = !!vFlip?canvas.height:0;
    let _sx = !!hFlip?-1:1;
    let _sy = !!vFlip?-1:1;

    let _canvas = createCanvas(canvas.width, canvas.height);
    let ctx = _canvas.getContext('2d');

    ctx.translate(_tx,_ty);
    ctx.scale(_sx,_sy);
    ctx.drawImage(canvas, 0, 0);

    return _canvas;
  }

  const turnCanvas = (canvas,turn) =>{
    if(!turn) return canvas;

    let _width = canvas.width;
    let _height = canvas.height;

    if(turn === 90 || turn === 270){
      _width = canvas.height;
      _height = canvas.width;
    }

    let _canvas = createCanvas(_width, _height);
    let ctx = _canvas.getContext('2d');

    ctx.translate(_width/2,_height/2);
    ctx.rotate(turn*Math.PI/180);
    ctx.drawImage(canvas,-canvas.width/2,-canvas.height/2);

    return _canvas;
  }

  const rotateCanvas = (canvas,rotate) =>{
    if(!rotate) return canvas;

    let _canvas = createCanvas(canvas.width, canvas.height);
    let ctx = _canvas.getContext('2d');

    let _w = rotatedWidth(canvas.width,canvas.height,rotate);
    let _scale = _w/canvas.width;

    ctx.translate(canvas.width/2, canvas.height/2);
    ctx.scale(_scale, _scale);
    ctx.rotate(rotate * Math.PI / 180);
    ctx.translate(-canvas.width/2, -canvas.height/2);
    ctx.drawImage(canvas,0,0);

    return _canvas;
  }

  const cropCanvas = (canvas,left,top,width,height) =>{
    if(!width || width > 1 || !height || height > 1) return canvas;

    left = Math.floor(left*canvas.width);
    top = Math.floor(top*canvas.height);
    width = Math.ceil(width*canvas.width);
    height = Math.ceil(height*canvas.height);

    let _canvas = createCanvas(width,height);
    let ctx = _canvas.getContext('2d');
    ctx.drawImage(canvas,left,top,width,height,0,0,width,height);

    return _canvas;
  }

  const resizeCanvas = (canvas,width,height) =>{
    if(!width || (width === canvas.width && height === canvas.height)) return canvas;

    let _canvas = createCanvas(width,height);
    let ctx = _canvas.getContext('2d');
    ctx.drawImage(canvas,0,0,width,height);

    return _canvas;
  }

  const drawImage = (canvas,cropCord) =>{
    cropCord = cropCord || [0,0,0,0,0,0,false,false];
    let [left,top,width,height,rotate,turn,hFlip,vFlip] = cropCord;

    canvas = flipCanvas(canvas,hFlip,vFlip);
    canvas = turnCanvas(canvas,turn);
    canvas = rotateCanvas(canvas,rotate);
    canvas = cropCanvas(canvas,left,top,width,height);

    return canvas;
  }

  const drawImages = (canvas,cropCords) =>{
    if(!cropCords.length) return canvas;

    let cropCord = cropCords.shift()
    canvas = drawImage(canvas,cropCord);

    return drawImages(canvas,cropCords)
  }

  const finalDrawImage = async(file,canvas,width,height,cropCords) =>{
    try {

      let image = await loadImage(URL.createObjectURL(file))
      let cw = image.width;
      let ch = image.height;

      let _canvas = createCanvas(cw,ch);
      let ctx = _canvas.getContext('2d');
      ctx.drawImage(image,0,0,cw,ch);

      _canvas = drawImages(_canvas,JSON.parse(JSON.stringify(cropCords)));
      _canvas = resizeCanvas(_canvas,width,height);

      return _canvas;
    } catch (e) {
      // console.log(e);
      return canvas;
    }
  }

  const defaultDrawImage = async(file,cropCords) =>{
    try {
      let image = await loadImage(URL.createObjectURL(file))
      let [w,h] = getViewSize(image.width, image.height);

      let _canvas = createCanvas(w,h);
      let ctx = _canvas.getContext('2d');
      ctx.drawImage(image, 0, 0,w,h);

      _canvas = drawImages(_canvas,JSON.parse(JSON.stringify(cropCords)));

      return _canvas;
    } catch (e) {
      return null;
    }
  }

let data = {
  getViewSize,
  getCss,

  downloadFile,
  drawImage,
  finalDrawImage,
  defaultDrawImage,

}

export default data;
