import { createCanvas, loadImage } from 'canvas';

  let Transform = {


    methods :[
      {name:"auto",child:2},
      {name:"grayscale",child:9},
      {name:"sharpen",child:5},
      {name:"blur",child:2},
      {name:"sobel",child:8},
      {name:"prewitt",child:8}
    ],

    matrices : [
      [
        0, -1,  0,
       -1,  4, -1,
        0, -1,  0 //Ridge or edge detection
      ],[
       -1, -1, -1,
       -1,  8, -1,
       -1, -1, -1 //Ridge or edge detection
      ],[
        0, -1,  0,
       -1,  5, -1,
        0, -1,  0 //sharpen
      ],[
       -1, -1, -1,
       -1,  9, -1,
       -1, -1, -1 //sharpen
      ],[
        1/9, 1/9, 1/9,
        1/9, 1/9, 1/9,
        1/9, 1/9, 1/9 //mean blur
      ],[
        1/16, 2/16, 1/16,
        2/16, 4/16, 2/16,
        1/16, 2/16, 1/16 //Gaussian blur
      ],[
        0, 1,  0,
        1, -4, 1,
        0, 1,  0 // laplacian
      ],[
       -1, -2,  -1,
        0, 0, 0,
        1, 2,  1 //Sobel Transform - y (Edge Detection)
      ],[
       -1, 0, 1,
       -2, 0, 2,
       -1, 0, 1 //Sobel Transform - x (Edge Detection)
      ],[
       -1, -1,  -1,
        0, 0, 0,
        1, 1,  1 //Prewitt filter -y (Edge Detection)
      ],[
       -1, 0, 1,
       -1, 0, 1,
       -1, 0, 1 //Prewitt filter -x (Edge Detection)
      ]
    ],

    createImageData : (width, height) =>{
      let canvas = createCanvas(width, height);
      let ctx = canvas.getContext('2d');
      return ctx.getImageData(0, 0, width, height);
    },

    grayscale : (pixels,type=0) =>{
      //type 0 for Luminosity, 1 classic ,2lightness ,3 greyish, 4 b&w
      let d = pixels.data;
      for (let i=0; i<d.length; i+=4) {
        let r = d[i];
        let g = d[i+1];
        let b = d[i+2];
        let v = 0;

        if(type === 0){
          // CIE luminance for the RGB
          // The human eye is bad at seeing red and blue, so we de-emphasize them.
          v = 0.2126*r + 0.7152*g + 0.0722*b;
        }else if (type === 1) {
          v = 0.299*r + 0.587*g + 0.114*b;
        }else if (type === 2) {
          v = r+g+b/3;
        }else if (type === 3) {
          v = (Math.max(r, g, b) + Math.min(r, g, b)) / 2;
        }else if (type === 4) {
          v = r;
        }else if (type === 5) {
          v = g;
        }else if (type === 6) {
          v = b;
        }else if (type === 7) {
          v = r+g+b;
          v = v>510?255:v>255?127.5:0
        }else if (type === 8) {
          v = r+g+b;
          v = v>383?255:0
        }
        d[i] = d[i+1] = d[i+2] = v;
      }
      return pixels;
    },

    convolute : (width, height,pixels,weights) =>{
      let side = Math.round(Math.sqrt(weights.length));
      let halfSide = Math.floor(side/2);
      let src = pixels.data;

      let output = Transform.createImageData(width, height);
      let dst = output.data;

      for (let sy=0; sy<height; sy++) {
        for (let sx=0; sx<width; sx++) {
          let dstOff = (sy*width+sx)*4;
          // calculate the weighed sum of the source image pixels that
          // fall under the convolution matrix
          let r=0, g=0, b=0, a=0;
          for (let cy=0; cy<side; cy++) {
            for (let cx=0; cx<side; cx++) {
              let scy = sy + cy - halfSide;
              let scx = sx + cx - halfSide;
              if (scy >= 0 && scy < height && scx >= 0 && scx < width) {
                let srcOff = (scy*width+scx)*4;
                let wt = weights[cy*side+cx];
                r += src[srcOff] * wt;
                g += src[srcOff+1] * wt;
                b += src[srcOff+2] * wt;
                // a += src[srcOff+3] * wt;
              }
            }
          }

          dst[dstOff] = r;
          dst[dstOff+1] = g;
          dst[dstOff+2] = b;
          dst[dstOff+3] = src[dstOff+3];
        }
      }
      return output;
    },

    doubleConvolute : (width, height,pixels,kernel1,kernel2,index) =>{
      let side = Math.round(Math.sqrt(kernel1.length));
      let halfSide = Math.floor(side/2);
      let src = pixels.data;

      let output = Transform.createImageData(width, height);
      let dst = output.data;

      for (let sy=0; sy<height; sy++) {
        for (let sx=0; sx<width; sx++) {
          let dstOff = (sy*width+sx)*4;
          let v=0, h=0;
          for (let cy=0; cy<side; cy++) {
            for (let cx=0; cx<side; cx++) {
              let scy = sy + cy - halfSide;
              let scx = sx + cx - halfSide;
              if (scy >= 0 && scy < height && scx >= 0 && scx < width) {
                let srcOff = (scy*width+scx)*4;
                v += src[srcOff] * kernel1[cy*side+cx];
                h += src[srcOff] * kernel2[cy*side+cx];
              }
            }
          }

          let _val = Math.sqrt((v**2) + (h**2));
          let r = _val,g = _val,b = _val;

          if(index === 1){r=0;}
          else if (index === 2) {g=0;}
          else if (index === 2) {b=0;}
          else if (index === 3) {r=0;g=0;}
          else if (index === 4) {r=0;b=0;}
          else if (index === 5) {b=0;g=0;}
          else if (index === 6) {r=v;g=h;b=(v+h)/4;}
          else if (index === 7) {r=h;g=v;b=(v+h)/4;}

          dst[dstOff] = r;
          dst[dstOff+1] = g;
          dst[dstOff+2] = b;
          dst[dstOff+3] = 255;
        }
      }
      return output;
    },

    smartEdit : (width, height,pixels,index) =>{
      let d = pixels.data;
      for (let i=0; i<d.length; i+=4){
        let r = d[i];
        let g = d[i+1];
        let b = d[i+2];

        if(index == 0){
          let _max = Math.max(r,g,b) || 1;
          let _f = Math.sqrt(255/_max);
          r = r*_f; g = g*_f; b = b*_f;
        }else if (index == 1) {
          let _max = Math.max(r,g,b) || 1;
          _max = _max > 200?_max:200;
          let _f = 255/_max;
          r = r*_f; g = g*_f; b = b*_f;
        }


        d[i] = r;
        d[i+1] = g;
        d[i+2] = b;
      }
      return pixels;
    },

    apply : (ctx,filter={}) =>{
      if(!filter.name) return;

      let width = ctx.canvas.width;
      let height = ctx.canvas.height;

      let image = ctx.getImageData(0, 0, width, height);
      let index = filter?.index??0;

      if (filter.name === "grayscale") {
        image = Transform.grayscale(image,index);
      }else if (filter.name === "sharpen") {
        let _i = index === 0?6:index-1;
        if(index < 3){
          image = Transform.grayscale(image);
        }
        image = Transform.convolute(width, height,image,Transform.matrices[_i]);
      }else if (filter.name === "blur") {
        let _i = index === 0?4:5;
        image = Transform.convolute(width, height,image,Transform.matrices[_i]);
      }else if (filter.name === "sobel") {
        image = Transform.grayscale(image);
        image = Transform.doubleConvolute(width, height,image,Transform.matrices[7],Transform.matrices[8],index);
      }else if (filter.name === "prewitt") {
        image = Transform.grayscale(image);
        image = Transform.doubleConvolute(width, height,image,Transform.matrices[9],Transform.matrices[10],index);
      }else if (filter.name === "auto") {
        image = Transform.smartEdit(width, height,image,index);
      }
      ctx.putImageData(image, 0, 0);

    },

    generateSprite : async() =>{

      let child = Transform.methods.length;
      let psize = 60;
      let csize = 40;

      let height = psize + (child*csize);
      let width = child*psize;
      let largest = Math.max.apply(0, Transform.methods.map(i=>i.child));
      if(largest*csize > width){
        width = largest*csize;
      }


      let canvas = createCanvas(width, height);
      let ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      let img = new Image();
      await new Promise(r => img.onload=r, img.src="../img/filter2.jpg");

      for (let i = 0; i < child; i++) {
        let _filter = Transform.methods[i];

        let _canvas = createCanvas(psize, psize);
        let _ctx = _canvas.getContext('2d');
        _ctx.drawImage(img,0,0,psize,psize);
        Transform.apply(_ctx,_filter);
        ctx.drawImage(_canvas,i*psize,0);

        for (let j = 0; j < _filter.child; j++) {
          let childcanvas = createCanvas(csize, csize);
          let childctx = childcanvas.getContext('2d');
          childctx.drawImage(img,0,0,csize,csize);
          Transform.apply(childctx,{name:_filter.name,index:j});
          ctx.drawImage(childcanvas,j*csize,psize+i*csize);

        }
      }

      // return canvas;

      let _a = document.createElement("a");
      _a.download = "transform.jpg";
      _a.href = canvas.toDataURL();
      _a.target = '_blank';
      _a.style.display = 'none';
      document.body.appendChild(_a);
      _a.click();
      document.body.removeChild(_a);
    },

  }



  // Transform.generateSprite();



export default Transform;
