    adjustCss:(arg={})=>{

      let {brightness,saturate,invert,contrast,sepia,blur,hue,grayscale} = arg;

      brightness = brightness || 100;
      saturate = saturate || 100;
      invert = invert || 0;
      contrast = contrast || 100;
      sepia = sepia || 0;
      blur = blur || 0;
      hue = hue || 0;
      grayscale = grayscale || 0;

      let _filter = Object.keys(arg).map((name,i)=>{
        if(name === "brightness" && brightness !== 100){
          return `brightness(${brightness}%)`;
        }else if(name === "saturate" && saturate !== 100){
          return `saturate(${saturate}%)`;
        }else if(name === "invert" && !!invert){
          return `invert(${invert}%)`;
        }else if(name === "contrast" && contrast !== 100){
          return `contrast(${contrast}%)`;
        }else if(name === "sepia" && !!sepia){
          return `sepia(${sepia}%)`;
        }else if(name === "blur" && !!blur){
          return `blur(${blur}px)`;
        }else if(name === "hue" && !!hue){
          return `hue-rotate(${hue}deg)`;
        }else if(name === "grayscale" && !!grayscale){
          return `grayscale(${grayscale}%)`;
        }else{
          return "";
        }
      }).filter(i=> !!i).join(" ");

      return {filter:_filter}
    },
    adjust:(canvas,arg={})=>{
      let _filter = Filter.adjustCss(arg);
      _filter = _filter.filter
      if(!!_filter){
        let _canvas = createCanvas(canvas.width,canvas.height);
        let ctx = _canvas.getContext('2d');
        ctx.filter = _filter;
        ctx.drawImage(canvas,0,0);
        return _canvas;
      }
      return canvas;
    },


