import React, {Component,createRef} from 'react'
import { createCanvas, loadImage } from 'canvas';
import 'pepjs'
import {Utils} from '../../helper'

import './crop.scss';


class CropBox extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.blank = {
      type:"",
      cord:{x:0,y:0},
    }
    this.ref = createRef(null);

    this.state = this.blank;
    this.sizes = {"cc":[0,0],"c11":[1,1],"c34":[3,4],"c43":[4,3],"c23":[2,3],"c32":[3,2]};
  }


  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentDidMount(){
    let {crop,boxWidth,boxHeight} = this.props?.data??{};
    if(!crop) return;

      let top = 0;
      let left = 0;
      let width = boxWidth;
      let height = boxHeight;

      if(crop !== "cc"){
        let _ratio = this.sizes[crop][0]/this.sizes[crop][1]
        if (width >= height) {
          height = Math.ceil(height/2);
          width = Math.ceil(height*_ratio);
        }else {
          width = Math.ceil(width/2);
          height = Math.ceil(width/_ratio);
        }
      }
      top = Math.floor((boxHeight-height)/2);
      left = Math.floor((boxWidth-width)/2);
      this.props.handler({top,left,width,height});
  }
  componentWillUnmount(){
    this.props.handler({top:0,left:0,width:0,height:0});
  }

   moveMouseDown = event =>{
    let{isProcess} = this.props?.data??{};
    if(!!isProcess) return;
    let{clientX,clientY} = event;
    clientX = clientX || 0;
    clientY = clientY || 0;
    this.setState({cord:{x:clientX,y:clientY},type:event.target.className});
  }

  moveMouseUp = event =>{
    this.setState({cord:{x:0,y:0},type:""});
  }

  moveMouseMove = async(event) =>{
    let{type,cord} = this.state;
    if(!type) return;

    let{clientX,clientY} = event;
    clientX = clientX || 0;
    clientY = clientY || 0;
    let _diffX = clientX - cord.x;
    let _diffY = clientY - cord.y;

    this.setState({cord:{x:clientX,y:clientY}});


    if(type === "move"){
      this.handleMove(_diffX,_diffY);
    }else if (type === "top") {
      this.handleTop(_diffY);
    }else if (type === "right") {
      this.handleRight(_diffX);
    }else if (type === "bottom") {
      this.handleBottom(_diffY);
    }else if (type === "left") {
      this.handleLeft(_diffX)
    }else if (type === "top_left") {
      this.handleTopLeft(_diffX,_diffY);
    }else if (type === "top_right") {
      this.handleTopRight(_diffX,_diffY);
    }else if (type === "bottom_right") {
      this.handleBottomRight(_diffX,_diffY);
    }else if (type === "bottom_left") {
      this.handleBottomLeft(_diffX,_diffY);
    }
  }

  handleMove = (x,y) =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;

    top = top + y;
    top = top <0?0:top+height>boxHeight?boxHeight-height:top;
    left = left + x;
    left = left <0?0:left+width>boxWidth?boxWidth-width:left;

    this.props.handler({left,top});
  }
  handleTop = y =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";

    y = top+y<0?-top:y;
    let _height = height - y;
    _height = _height > 80?_height:80;

    if(!!crop && crop !== "cc"){
      let _ratio = this.sizes[crop][0]/this.sizes[crop][1];

      let _width = Math.floor(_height*_ratio);
      if (_width > boxWidth) {
        _width = boxWidth;
        _height = Math.floor(_width/_ratio);
      }else if (_width < 80) {
        _width = 80;
        _height = Math.floor(_width/_ratio);
      }

      let _left = Math.floor(left+((width-_width)/2));
      _left = _left < 0?0:_width+_left>boxWidth?boxWidth-_width:_left;

      this.props.handler({left:_left,width:_width})
    }
    this.props.handler({top:top+height-_height,height:_height})
  }
  handleRight = x =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";

    let _width = width + x;
    _width = _width < 80?80:left+_width>boxWidth?boxWidth-left:_width;

    if(!!crop && crop !== "cc"){
      let _ratio = this.sizes[crop][1]/this.sizes[crop][0];

      let _height = Math.floor(_width*_ratio);
      if (_height > boxHeight) {
        _height = boxHeight;
        _width = Math.floor(_height/_ratio);
      }else if (_height < 80) {
        _height = 80;
        _width = Math.floor(_height/_ratio);
      }

      let _top = Math.floor(top+((height-_height)/2));
      _top = _top < 0?0:_height+_top>boxHeight?boxHeight-_height:_top;

      this.props.handler({top:_top,height:_height})
    }
    this.props.handler({width:_width})
  }
  handleBottom = y =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";

    let _height = height + y;
    _height = _height < 80?80:top+_height>boxHeight?boxHeight-top:_height;

    if(!!crop && crop !== "cc"){
      let _ratio = this.sizes[crop][0]/this.sizes[crop][1];

      let _width = Math.floor(_height*_ratio);
      if (_width > boxWidth) {
        _width = boxWidth;
        _height = Math.floor(_width/_ratio);
      }else if (_width < 80) {
        _width = 80;
        _height = Math.floor(_width/_ratio);
      }

      let _left = Math.floor(left+((width-_width)/2));
      _left = _left < 0?0:_width+_left>boxWidth?boxWidth-_width:_left;

      this.props.handler({left:_left,width:_width})
    }
    this.props.handler({height:_height})
  }
  handleLeft = x =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";

    x = left+x<0?-left:x;
    let _width = width - x;
    _width = _width > 80?_width:80;

    if(!!crop && crop !== "cc"){
      let _ratio = this.sizes[crop][1]/this.sizes[crop][0];

      let _height = Math.floor(_width*_ratio);
      if (_height > boxHeight) {
        _height = boxHeight;
        _width = Math.floor(_height/_ratio);
      }else if (_height < 80) {
        _height = 80;
        _width = Math.floor(_height/_ratio);
      }

      let _top = Math.floor(top+((height-_height)/2));
      _top = _top < 0?0:_height+_top>boxHeight?boxHeight-_height:_top;

      this.props.handler({top:_top,height:_height})
    }
    this.props.handler({left:left+width-_width,width:_width})
  }
  handleTopLeft = (x,y) =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data
    let crop = this.props?.data?.crop??"";

    y = top+y<0?-top:y;
    let _height = height - y;
    _height = _height > 80?_height:80;

    x = left+x<0?-left:x;
    let _width = width - x;
    _width = _width > 80?_width:80;


    if(!!crop && crop !== "cc"){
      let _max = Math.max(Math.abs(x),Math.abs(y)) === Math.abs(x)?-x:-y;

      let _ratio = this.sizes[crop][0]/this.sizes[crop][1];
      _height = height + _max;
      _height = _height < 80?80:top+height<_height?top+height:_height;

      _width = Math.floor(_height*_ratio);
      if (left+width < _width) {
        _width = left+width;
        _height = Math.floor(_width/_ratio);
      }else if (_width < 80) {
        _width = 80;
        _height = Math.floor(_width/_ratio);
      }
    }
    this.props.handler({left:left+width-_width,top:top+height-_height,width:_width,height:_height})
  }
  handleTopRight = (x,y) =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";


    y = top+y<0?-top:y;
    let _height = height - y;
    _height = _height > 80?_height:80;

    let _width = width + x;
    _width = _width < 80?80:left+_width>boxWidth?boxWidth-left:_width;

    if(!!crop && crop !== "cc"){
      let _max = Math.max(Math.abs(x),Math.abs(y)) === Math.abs(x)?x:-y;

      let _ratio = this.sizes[crop][0]/this.sizes[crop][1];
      _height = height + _max;
      _height = _height < 80?80:top+height<_height?top+height:_height;

      _width = Math.floor(_height*_ratio);
      if (left+_width > boxWidth) {
        _width = boxWidth-left;
        _height = Math.floor(_width/_ratio);
      }else if (_width < 80) {
        _width = 80;
        _height = Math.floor(_width/_ratio);
      }
    }
    this.props.handler({top:top+height-_height,width:_width,height:_height})
  }

  handleBottomRight = (x,y) =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";

    let _height = height + y;
    _height = _height < 80?80:top+_height>boxHeight?boxHeight-top:_height;
    let _width = width + x;
    _width = _width < 80?80:left+_width>boxWidth?boxWidth-left:_width;

    if(!!crop && crop !== "cc"){
      let _max = Math.max(Math.abs(x),Math.abs(y)) === Math.abs(x)?x:y;

      let _ratio = this.sizes[crop][0]/this.sizes[crop][1];
      _height = height + _max;
      _height = _height < 80?80:top+_height>boxHeight?boxHeight-top:_height;

      _width = Math.floor(_height*_ratio);
      if (left+_width > boxWidth) {
        _width = boxWidth-left;
        _height = Math.floor(_width/_ratio);
      }else if (_width < 80) {
        _width = 80;
        _height = Math.floor(_width/_ratio);
      }
    }
    this.props.handler({width:_width,height:_height})
  }
  handleBottomLeft = (x,y) =>{
    let{left,top,width,height,boxWidth,boxHeight} = this.props.data;
    let crop = this.props?.data?.crop??"";


    let _height = height + y;
    _height = _height < 80?80:top+_height>boxHeight?boxHeight-top:_height;

    x = left+x<0?-left:x;
    let _width = width - x;
    _width = _width > 80?_width:80;

    if(!!crop && crop !== "cc"){
      let _max = Math.max(Math.abs(x),Math.abs(y)) === Math.abs(x)?-x:y;

      let _ratio = this.sizes[crop][0]/this.sizes[crop][1];
      _height = height + _max;
      _height = _height < 80?80:top+_height>boxHeight?boxHeight-top:_height;
      _width = Math.floor(_height*_ratio);
      if (_width > width+left) {
        _width = width+left;
        _height = Math.floor(_width/_ratio);
      }else if (_width < 80) {
        _width = 80;
        _height = Math.floor(_width/_ratio);
      }
    }
    this.props.handler({left:left+width-_width,width:_width,height:_height})
  }


  render(){
    let {type} = this.state;
    let{top,left,width,height,crop} = this.props?.data??{};
    let _iw = width >= 200 && height >= 200?"40px":width >= 150 && height >= 150?"30px":"20px";
      return(
        <div className="crop__container"
             ref={this.ref}
             touch-action={`${!!crop?"none":"auto"}`}
             onPointerMove = {this.moveMouseMove}
             onPointerUp = {this.moveMouseUp}
             onPointerLeave = {this.moveMouseUp}
        >
          <div className="crop__shadow">
            <span className="top__shadow" style={{width,left,height:top}}></span>
            <span className="right__shadow" style={{left:width+left}}></span>
            <span className="bottom__shadow" style={{width,left,top:height+top}}></span>
            <span className="left__shadow" style={{width:left}}></span>
          </div>

          <div className="crop__box" style={{top,left,width,height,"--iw":_iw}}>
            <div className={`move${type==="move"?" active":""}`} onPointerDown={this.moveMouseDown}>+</div>
            <button className={`top${type==="top"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`left${type==="left"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`right${type==="right"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`bottom${type==="bottom"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`top_left${type==="top_left"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`top_right${type==="top_right"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`bottom_left${type==="bottom_left"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
            <button className={`bottom_right${type==="bottom_right"?" active":""}`} onPointerDown={this.moveMouseDown}>&nbsp;</button>
          </div>

        </div>
      )
  }
}

export default CropBox
