import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import $ from "jquery";
import {Utils} from '../../helper'

import './style.scss';

class Home extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.state = {
      scrolling:false,
    }
  }

  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentDidMount(){
    document.title = "Editor | Home";


  }
  componentWillUnmount(){
    this.unmounted = true;
    document.title = "Editor | Home";
  }

  renderBtns = () =>{
    let _btns = [
      ["Edit Image","edit-image"],
      ["Photo Card","photo-card"],
      ["Image to Pdf","image-to-pdf"],
      ["Pdf to Image","pdf-to-image"],
      ["Enhancer  Image","enhancer -image"],
      ["Create  Collage","create-collage"]
    ];


    return _btns.map((_btn,i)=>{
      return <div className="home__btn" key={i}><a href={`./#/${_btn[1]}`}><span>{_btn[0]}</span><i></i></a></div>
    })
  }


  render(){

    return(
      <div className="section__model">
        <div className="home__container">
        {this.renderBtns()}
        </div>
      </div>
    )
  }
}

export default Home;
