import React, {Component,createRef} from 'react'

import './styles.scss';
import {Filter,Draw,Utils} from '../../helper'

import CropBox from './crop.jsx';

import {ReactComponent as SelectIcon} from '../../images/select.svg';
import {ReactComponent as UndoIcon} from '../../images/undo.svg';
import {ReactComponent as DownloadIcon} from '../../images/download.svg';
import {ReactComponent as CrossIcon} from '../../images/cross.svg';
import {ReactComponent as TickIcon} from '../../images/tick.svg';
import {ReactComponent as ResetIcon} from '../../images/reset.svg';
import {ReactComponent as FlipIcon} from '../../images/flip.svg';
import {ReactComponent as Flip2Icon} from '../../images/flip2.svg';

class ImagePreview extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.blank = {
      top:0,
      left:0,
      width:0,
      height:0,
      rotate:0,
      boxWidth:0,
      boxHeight:0,

      turn:0,
      hFlip:false,
      vFlip:false
    }

    this.state = this.blank;
    this.ref = createRef(null);
  }


  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentWillUnmount(){

  }
  componentDidMount(){
    let {canvas} = this.props?.data??{};
    if(!canvas) return;
    let boxWidth = this.ref.current.clientWidth;
    let boxHeight = Math.floor(boxWidth *canvas.height/canvas.width);
    let maxHeight = window.innerHeight -250;

    if(boxHeight > maxHeight){
      boxHeight = maxHeight
      boxWidth = Math.floor(maxHeight *canvas.width/canvas.height);
    }

    this.ref.current.style.width = `${boxWidth+4}px`;
    this.ref.current.style.height = `${boxHeight+4}px`;
    this.setState({boxWidth,boxHeight});
  }


  closeEditing = () =>{
    let{boxWidth,boxHeight,turn} = this.state;
    let{isProcess} = this.props?.data??{};
    if(!!isProcess) return;

    if(turn === 90 || turn === 270){
      let _w = boxHeight;
      boxWidth = boxHeight;
      boxHeight = _w;
    }
    this.setState({hFlip:false,vFlip:false,rotate:0,boxWidth,boxHeight})
    this.props.close({crop:""})
  }

  saveEditing = async() =>{
    try {
      let{left,top,width,height,boxWidth,boxHeight,rotate,turn,hFlip,vFlip} = this.state;
      let{canvas,width:pw,height:ph,cropCords,isProcess} = this.props?.data??{};
      if(!!isProcess || !this.props.close) return;
      this.props.close({isProcess:true});

      let cropCord = [left/boxWidth, top/boxHeight, width/boxWidth,height/boxHeight, rotate,turn, hFlip,vFlip];
      cropCords.push(cropCord);
      let _newcanvas = Draw.drawImage(canvas,cropCord);


      let _width = Math.ceil(width*canvas.width/boxWidth);
      let _height = Math.ceil(height*canvas.height/boxHeight);

      let _pFactor = pw/canvas.width;
      pw = pw?Math.ceil(_pFactor*_width):_width;
      ph = ph?Math.ceil(_pFactor*_height):_height;


      _height = Math.floor(boxWidth*height/width);
      _width = boxWidth;
      let maxHeight = window.innerHeight - 250;

      if(_height > maxHeight){
        _height = maxHeight
        _width = Math.floor(maxHeight*width/height);
      }

      this.ref.current.style.width = `${_width+4}px`;
      this.ref.current.style.height = `${_height+4}px`;
      this.setState({hFlip:false,vFlip:false,rotate:0,turn:0,boxWidth:_width,boxHeight:_height})
      this.props.close({crop:"",canvas:_newcanvas,width:pw, height:ph,cropCords,isProcess:false})

    } catch (e) {
      this.props.close({isProcess:false})
      // console.log(e);
    }
  }

  handleRotate = event =>{
    let{isProcess} = this.props?.data??{};
    if(!!isProcess) return;
    let value = parseInt(event.target.value) || 0;
    this.setState({rotate:value})
  }
  handleTurn = event =>{
    let{turn,boxWidth,boxHeight} = this.state;
    let{isProcess} = this.props?.data??{};
    if(!!isProcess) return;
    let _w = boxHeight;
    let _h = boxWidth;

    this.setState({turn:(turn+90)%360,boxWidth:_w,boxHeight:_h},()=>{
      this.ref.current.style.width = `${_w+4}px`;
      this.ref.current.style.height = `${_h+4}px`;
    })
  }

  render(){
    let{top,left,width,height,rotate,boxWidth,boxHeight,turn,hFlip,vFlip} = this.state;
    let{file,canvas,crop,adjust,isProcess} = this.props?.data??{};
    let _url = !!canvas?canvas.toDataURL():URL.createObjectURL(file);

    let css1 = Filter.adjustCss(adjust);
    let css2 = Draw.getCss(boxWidth,boxHeight,rotate,hFlip,vFlip,turn);
    let imgCss = {...css1,...css2}

      return(
         <div className="image_preview">

         <div className="image_preview_options">
           <button onClick={()=>this.props.handler("new")} disabled={!!crop}><SelectIcon/></button>
           <button onClick={()=>this.props.handler("reset")} disabled={!!crop}><UndoIcon/></button>
           <button onClick={()=>this.props.handler("download")} disabled={!!crop}><DownloadIcon/></button>
         </div>

          <div className="image_preview_img"
            ref={this.ref}
           >
            <img src={_url} alt="img" style={imgCss}/>


            {!!isProcess && <div className="process__loader"></div>}

            {!!crop && <CropBox
            handler={(e)=>this.setState(e)}
            data={{top,left,width,height,crop,boxWidth,boxHeight,isProcess}}
            key={crop+turn}
            />}

          </div>

          {!!crop && <div className="image__crop__tools">
            <div className="image__rotate__bar">
              <input type="range" min="-45" max="45" value={rotate} onChange={this.handleRotate}/>
              <span>{rotate}</span>
            </div>

            <div className="image__crop__tool">
              <button className={`${!!hFlip?"active":""}`} onClick={()=>this.setState({hFlip:!hFlip})} disabled={!!isProcess}><FlipIcon/></button>
              <button className={`${!!vFlip?"active":""}`} onClick={()=>this.setState({vFlip:!vFlip})} disabled={!!isProcess}><Flip2Icon/></button>
              <button className={`dark-icon ic-${turn}`} onClick={this.handleTurn}><ResetIcon/></button>
            </div>

            <div className="image__crop__finish">
              <button onClick={this.closeEditing}><CrossIcon/></button>
              <button onClick={this.saveEditing}><TickIcon/></button>
            </div>
          </div>}

        </div>
      )
  }
}

export {ImagePreview}
