import React, {Fragment } from 'react'

import './styles.scss';
import {ReactComponent as ImageIcon} from '../../images/image.svg';


function SelectImage(props) {

  const handleFile = event =>{
    let file = event.target.files[0];
    if(!file) return;
    props.handler(file);
  }

  return <div className="file__upload__wrap">
    <input id="file-input" type='file' accept="image/*" onChange={handleFile}/>
    <label htmlFor="file-input">
    <ImageIcon/>
      <p>Select an image</p>
    </label>
  </div>
}



export {SelectImage}
