
import React,{ useEffect ,Fragment} from 'react'
import { HashRouter ,Routes, Route ,useLocation,BrowserRouter} from 'react-router-dom';

import styled from 'styled-components'
import * as pages from '../pages'


function App() {

    return (
      <HashRouter>

        <Routes>

          <Route index  element={<pages.Home/>} />
          <Route path="/home" element={<pages.Home/>} />
          <Route path="/edit-image" element={<pages.EditImage/>} />
          <Route path="/photo-card" element={<pages.PhotoCard/>} />
          <Route path="/familty-tree" element={<pages.FamiltyTree/>} />

          <Route path='*' element={<pages.PageNotFound />} />

        </Routes>
      </HashRouter>
    )
}

export default App
