import React, { Component,Fragment} from 'react'
// import { Link } from 'react-router-dom'
import { createCanvas, loadImage } from 'canvas';

// import $ from "jquery";
import {Draw,Filter,Transform} from '../../helper'

import './style.scss';

import {FilePicker,FilePreview,EditOptions,FilterOptions} from '../../components'

import DummyImage from '../../images/adiyogi.webp';


class EditImage extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.blank = {
      menu:"crop",
      file:"",
      canvas:"",
      quality:90,
      width:0,
      height:0,
      formate:"jpg",
      hFlip:false,
      vFlip:false,
      turn:0,
      crop:"",
      cropCords:[],

      adjust:{},
      filter:{},
      transform:{},
      lastUse:"",

      isProcess:false,
    }

    this.state = this.blank
  }

  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentDidMount(){
    document.title = "Image Editor";

    // let _self = this;
    // fetch(DummyImage).then(res=>{
    //   res.blob().then((blob) => {
    //     const file = new File([blob], 'adiyogi.webp', {type: blob.type});
    //     _self.handleFile(file)
    //   })
    // })

  }

  componentWillUnmount(){
    this.unmounted = true;
    document.title = "Editor | Home";
  }

  handleFile = async(file) =>{
    try {
      // console.log(file);
      let image = await loadImage(URL.createObjectURL(file))

      let width = image.width;
      let height = image.height;

      let [w,h] = Draw.getViewSize(width, height);
      let canvas = createCanvas(w,h);
      let ctx = canvas.getContext('2d');

      ctx.drawImage(image, 0, 0,w,h);

      // Transform.apply(ctx,{name:"auto",index:1});


      this.setState({file,canvas,width,height});
    } catch (e) {
      this.setState(this.blank);
    }
  }

  handleOptions = (option) =>{
    let{file,isProcess} = this.state;
    if(!!isProcess) return;

    if(option === "new"){
      this.setState(this.blank);
    }else if (option === "reset") {
      this.setState(this.blank,()=>{
        this.handleFile(file);
      });
    }else if (option === "download") {
      this.handleDownload();
    }

  }

   handleDownload = async() =>{

     try {
      let{file,canvas,width, height,quality,formate,cropCords,adjust,filter,transform,lastUse,isProcess} = this.state;
      if(!!isProcess) return;
      this.setState({isProcess:true});

      let _canvas = await Draw.finalDrawImage(file,canvas,width,height,cropCords);
      let ctx = _canvas.getContext('2d');

      if(lastUse === "transform"){
        Filter.apply(ctx,filter);
        Transform.apply(ctx,transform);
      }else {
        Transform.apply(ctx,transform);
        Filter.apply(ctx,filter);
      }
      _canvas = Filter.adjust(_canvas,adjust);

      await Draw.downloadFile(_canvas,formate,quality);
      this.setState({isProcess:false});

     } catch (e) {
       console.log(e);
       this.setState({isProcess:false});
     }
  }

  handleFilter = async(value) =>{
    try {
      let{file,cropCords,isProcess,transform} = this.state;
      this.setState({isProcess:true,filter:value});

      let _canvas = await Draw.defaultDrawImage(file,cropCords);
      let ctx = _canvas.getContext('2d');
      Transform.apply(ctx,transform);
      Filter.apply(ctx,value);

      this.setState({canvas:_canvas,lastUse:"filter",isProcess:false});
    } catch (e) {
      console.log(e);
      this.setState({isProcess:false});
    }
  }

  handleAdvFilter = async(value) =>{
    try {
      let{file,cropCords,filter,isProcess} = this.state;
      this.setState({isProcess:true,transform:value});

      let _canvas = await Draw.defaultDrawImage(file,cropCords);
      let ctx = _canvas.getContext('2d');

      Filter.apply(ctx,filter);
      Transform.apply(ctx,value);

      this.setState({canvas:_canvas,lastUse:"transform",isProcess:false});
    } catch (e) {
      console.log(e);
      this.setState({isProcess:false});
    }
  }


  render(){
    let{menu,file,canvas,quality,width,height,formate,crop,cropCords,adjust,filter,transform,isProcess} = this.state;
    return(
      <div className="section__model section__model_row main__margin">
        {!file && <FilePicker.SelectImage handler={this.handleFile}/>}
        {!!file &&
          <Fragment>
            <div className="section__model_half">
              <FilePreview.ImagePreview
                handler={this.handleOptions}
                close={val=>this.setState(val)}
                data={{file,canvas,width,height,crop,cropCords,adjust,isProcess}}
              />
            </div>
            <div className="section__model_half">

              <EditOptions.RenderMenu handler={val=>this.setState(val)} data={{menu,isProcess}}/>


              {menu === "crop" && <EditOptions.EditBody>
                <EditOptions.CropOptions handler={val=>this.setState(val)} data={{quality,crop,isProcess}}/>
              </EditOptions.EditBody>}

              {menu === "adjust" && <FilterOptions.FilterBody>
                <FilterOptions.CssFilters handler={val=>this.setState(val)} data={{adjust,isProcess}} />
              </FilterOptions.FilterBody>}

              {menu === "filter" && <FilterOptions.FilterBody>
                <FilterOptions.CanvasFilters handler={this.handleFilter} data={{filter,isProcess}} />
              </FilterOptions.FilterBody>}

              {menu === "transform" && <FilterOptions.FilterBody>
                <FilterOptions.AdvFilters handler={this.handleAdvFilter} data={{transform,isProcess}} />
              </FilterOptions.FilterBody>}

              {menu === "setting" && <EditOptions.EditBody>
                <EditOptions.QualityFactor handler={val=>this.setState(val)} data={{quality,isProcess}}/>
                <EditOptions.FixedSizeInput handler={val=>this.setState(val)} data={{canvas,width,height,isProcess}}/>
                <EditOptions.FormateSelector handler={val=>this.setState(val)} data={{formate,isProcess}}/>
              </EditOptions.EditBody>}


            </div>
          </Fragment>
        }
      </div>
    )
  }
}

export default EditImage;
